	export default {
		data() {
			return {
				style: {
					pageHeight: 0,
					contentViewHeight: 0
				}
			}
		},
		methods: {
            toChangePhone(){
                uni.navigateTo({
                    url:"/pages/changePhone/changePhone",
                    animationType:"slide-in-right"
                });
            },
			toChangePwd(){
                uni.navigateTo({
                    url:"/pages/changePwd/changePwd",
                    animationType:"slide-in-right"
                });
            }
		},
        onLoad() {
            const info = uni.getSystemInfoSync();
            this.style.pageHeight = info.windowHeight;
            this.style.contentViewHeight = info.windowHeight; //像素
        }
	}
